@import "../../styles/_mixin";

.contentsWrap{
    margin: 10rem auto;
    max-width: 76.8rem;
    min-height: 50rem;
    border-radius: 3px;
    background:#fff;
    box-shadow: 0 0.3rem 0.4rem 0 rgba(0, 0, 0, 0.06);
    position: relative;
    z-index: 1;
}

h2 {
    img{
        width: 12rem;
        display: block;
        margin: 0 auto;
    }
    font-size: 2.2rem;
    text-align: center;
    padding: 3.5rem 0;
    border-bottom: 1px solid #e3e3e3;
    color: #333;
}

article{
    border-bottom: 1px solid #e3e3e3;
    padding:2.5rem 1.5rem;
    
}

.myfinoUl {
    li{
        font-size:1.65rem;
        padding:1rem 0;
        .or {
            font-size:1.45rem;
            color:#666;
        }
        span {
            display: inline-block;
            padding: .4rem .6rem;
            width: 12rem;
            border-radius: 3px;
            color: #333;
            background: #e3e3e3;
            margin-right: 1rem;
        }
    }
}

.myfinoDl{
    display: flex;
    align-items: center;
    padding-left: 13.1rem;
    margin: .5rem 0;
    font-size: 1.55rem;
    color:#555;
    @media screen and (max-width : 540px) {
        padding-left: 0;
    }
    dt{
        display: inline-block;
        img {
            position: relative;
            top: .2rem;
            left: .3rem;
            width: 1.4rem;
        }
    }
    dd{
        display: inline-block;
        line-height: 1.6;
        img{
            position: relative;
            top:.3rem;
            left:.3rem;
            width: 1.4rem;
        }
        a {
            @include ahref;
        }
    }
    em{
        font-style: normal;
        color:#777;
        padding:0 .3rem;
    }
}



.tit{
    font-size:1.8rem;
    color:#282c35;
    margin-bottom:1.6rem;
    span{
        font-weight: bold;
    }
}